	module.exports = {"data":{"markdownRemark":{"html":"<h1>First Attempt</h1>\n<ul>\n<li>2 heaping cups of dried pinto beans (~1 pound)</li>\n</ul>\n<p>Soak them overnight. If I think about it, I change the water once.</p>\n<p>The next day, dump the beans in the pot and fill with water until they're covered by a couple inches. I dash in some hot sauce and some salt.</p>\n<p>Cook on high pressure for 12 minutes.</p>\n<p>When it's done, do a natural pressure release for 12 minutes, then quick release the rest.</p>\n<p>When they were done, drain them, then add them back to the pot with some spices (cumin, salt, pepper) and mash them up.</p>\n<p><em>Verdict:</em> Cook them a little bit longer next time, maybe 14 minutes... there were still a few harder ones left.</p>","frontmatter":{"title":"Instant Pot Refried Beans","date":"December 15, 2017","path":"/instant-pot-refried-beans","tags":["instant pot","recipe"],"excerpt":""}}},"pathContext":{"next":null,"prev":{"html":"<ul>\n<li>1 Cup of quinoa, rinsed</li>\n<li>1/2 Cup of El Pato Salsa (about half the can)</li>\n<li>1/2 Cup of water</li>\n<li>Handful of frozen corn</li>\n<li>Handful of <em>cooked</em> black beans (from can or homemade)</li>\n<li>Salt, maybe some cumin</li>\n</ul>\n<h4>Instructions</h4>\n<p>Put Instant Pot in sautee mode. Dump the quinoa into the pot, no liquid, and stir it up until it smells toasty. Add the sauce, water, and corn and beans if you want.</p>\n<p>Seal the Instant Pot, and do high pressure for 1 minute.</p>\n<p>When it beeps, hit cancel to turn off \"Keep Warm\" mode, and wait for pressure to drop so you can open the lid, add salt &#x26; cumin as desired, and eat it!</p>","id":"/Users/taylor/Documents/Projects/taylorlistens/src/pages/2017-12-15-instant-pot-mexican-quinoa/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2017-12-15T15:39:10.752Z","path":"/instant-pot-mexican-quinoa","title":"Instant Pot Mexican Quinoa","excerpt":null,"tags":["instant pot","recipe"]}}}}

